import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import {
  Button,
  Form,
  Container,
  Nav,
  NavDropdown,
  Navbar,
} from "react-bootstrap";
import Home from "./page/Home";
import Review from "./page/Review";
import Summer from "./page/Summer";
import Winter from "./page/Winter";
import Footer from "./page/Footer";
import Detail from "./page/Detail";
import CustomSwiper from "./CustomSwiper";
import Card from "./components/Card";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import data from "./data";

const ProductList = ({ type }) => {
  const filteredData = data.filter((item) => item.type === type);
  return (
    <div>
      {type === "perfume" ? (
        <img
          className="banner"
          src={process.env.PUBLIC_URL + "/img/perfumeV.webp"}
          alt="Perfume"
        />
      ) : (
        <img
          className="banner"
          src={process.env.PUBLIC_URL + "/img/bodyV.webp"}
          alt="Body Care"
        />
      )}
      <h2>
        {type === "perfume" ? "Perfume Collection" : "Body Care Collection"}
      </h2>
      <div className="card-container">
        {filteredData.map((item, index) => (
          <Card key={index} item={item} />
        ))}
      </div>
    </div>
  );
};

function App() {
  const [query, setQuery] = useState("");
  const [search, setSearch] = useState("");

  const searchChange = (e) => {
    setQuery(e.target.value);
  };

  const searchKey = (e) => {
    if (e.key === "Enter") {
      setSearch(query);
    }
  };

  const searchButton = (e) => {
    e.preventDefault();
    setSearch(query);
  };

  const filterData = data.filter((item) => {
    return item.title.includes(search);
  });

  return (
    <div className="App">
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Brand as={Link} to="/home">
            Santa Maria Novella
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "100px" }}
              navbarScroll
            >
              <Nav.Link as={Link} to="/perfume">
                
              </Nav.Link>
              <Nav.Link as={Link} to="/body">
                 
              </Nav.Link>
              <NavDropdown title="event" id="navbarScrollingDropdown">
                <NavDropdown.Item as={Link} to="/summer">
                   
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/winter">
                   
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item as={Link} to="/review">
                   
                </NavDropdown.Item>
              </NavDropdown>
              <Nav.Link href="#" disabled>
                Link
              </Nav.Link>
            </Nav>
            <Form className="d-flex" onSubmit={searchButton}>
              <Form.Control
                type="search"
                placeholder="Search"
                value={query}
                onChange={searchChange}
                onKeyDown={searchKey}
                className="me-2"
                aria-label="Search"
              />
              <Button
                onClick={searchButton}
                type="submit"
                variant="outline-success"
              >
                Search
              </Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route path="/" element={<Home filterData={filterData} />} />
        <Route path="/home" element={<Home filterData={filterData} />} />
        <Route path="/perfume" element={<ProductList type="perfume" />} />
        <Route path="/body" element={<ProductList type="body" />} />
        <Route path="/summer" element={<Summer />} />
        <Route path="/winter" element={<Winter />} />
        <Route path="/review" element={<Review />} />
        <Route path="/product/:id" element={<Detail />} />
      </Routes>

      <Footer />
    </div>
  );
}

export default App;
